.LoadingTodo-card {
  display: grid;
  gap: 16px;
}

.LoadingTodo-card__item {
  border-radius: 12px;
  padding: 2em;
  display: grid;
  row-gap: 2em;
  animation: skeleton-loading 1s linear infinite alternate;
}

.LoadingTodo-card__name {
  height: 21px;
  border-radius: 30px;
  background-color: #f0f2f5;
}

.LoadingTodo-card__icon__container {
  display: grid;
  grid-template-columns: repeat(2, min-content);
  justify-content: end;
  gap: 0.5em;
}

.LoadingTodo-card__icon {
  width: 50px;
  height: 50px;
  background-color: #f0f2f5;
  border-radius: 50%;
  text-align: center;
  border: 1px solid transparent;
  display: grid;
  align-items: center;
  justify-content: center;
}

@keyframes skeleton-loading {
  0% {
    background-color: hsl(200, 20%, 90%);
  }
  100% {
    background-color: hsl(200, 20%, 95%);
  }
}
