.card__item {
  background-color: #fff;
  border-radius: 12px;
  padding: 2em;
  display: grid;
  row-gap: 2em;
}

.card__icon__container {
  display: grid;
  grid-template-columns: repeat(2, min-content);
  justify-content: end;
  gap: 0.5em;
}

.card__name--completed {
  color: #bbb;
  text-decoration: line-through;
}

.card__icon {
  width: 50px;
  height: 50px;
  background-color: transparent;
  border-radius: 50%;
  text-align: center;
  border: 1px solid #bbb;
  display: grid;
  align-items: center;
  justify-content: center;
}

.card__icon--complete {
  background-color: greenyellow;
  border-color: transparent;
}
